<template>
  <el-dialog
    v-model="dialogVisible"
    width="800px"
    :destroy-on-close="true"
    @opened="handleDialogOpened"
    @close="closeDialog"
  >
    <!--   ( Note) -->
    <div class="search-container">
      <el-input
        v-model="searchQuery"
        placeholder="Search in Note..."
        clearable
        class="search-input"
      />
    </div>

    <!--   -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="filteredData.length"
      :page-size="pageSize"
      :current-page="currentPage"
      @current-change="handlePageChange"
      class="pagination-container top-pagination"
    />

    <el-table
      :data="paginatedLogs"
      border
      stripe
      v-loading="loading"
      :key="tableKey"
    >
      <el-table-column
        prop="action"
        label="Action"
        width="200"
      ></el-table-column>
      <el-table-column
        prop="user_id"
        label="User ID"
        width="150"
      ></el-table-column>
      <el-table-column
        prop="str_logdate"
        label="Log Date"
        width="200"
      ></el-table-column>
      <el-table-column prop="note" label="Note"></el-table-column>
    </el-table>

    <!--   -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="filteredData.length"
      :page-size="pageSize"
      :current-page="currentPage"
      @current-change="handlePageChange"
      class="pagination-container bottom-pagination"
    />

    <template #footer>
      <div class="dialog-footer">
        <el-button @click="closeDialog">Close</el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { API_BASE_URL } from "@/config"; //  

export default {
  props: {
    visible: Boolean,
    userId: String, //  ID
  },
  data() {
    return {
      dialogVisible: false, // 
      logData: [], //  API  Log 
      loading: false,
      tableKey: 0, //  `el-table` 
      currentPage: 1, // 
      pageSize: 100, // 
      searchQuery: "", //   Note 
    };
  },
  computed: {
    // ****
    filteredData() {
      if (!this.searchQuery) {
        return this.logData;
      }
      const query = this.searchQuery.toLowerCase();
      return this.logData.filter(
        (log) => log.note && log.note.toLowerCase().includes(query)
      );
    },
    // ****
    paginatedLogs() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredData.slice(start, end);
    },
  },
  watch: {
    visible(newVal) {
      this.dialogVisible = newVal;
      if (newVal && this.userId) {
        this.fetchLogs(); // 
      }
    },
  },
  methods: {
    async fetchLogs() {
      if (!this.userId) {
        console.error(" : userId ");
        return;
      }
      this.loading = true;
      try {
        let token = localStorage.getItem("authToken");
        if (!token) {
          alert("Your session has expired. Please log in again.");
          this.$router.push("/login");
          return;
        }

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          user_id: this.userId, //  ID
        };

        console.log("  API  /Admin/EmployeeLog:", payload);
        const response = await axios.post(
          `${API_BASE_URL}/Admin/EmployeeLog`,
          payload
        );

        if (response.data.Code === 1000) {
          this.logData = response.data.data || [];
          console.log(" API :", this.logData);
          this.currentPage = 1; // 
          this.searchQuery = ""; // 
          this.refreshTable(); // 
        } else {
          this.$message.error("Failed to fetch logs: " + response.data.Code);
        }
      } catch (error) {
        console.error(" API Request Failed:", error);
        this.$message.error("Failed to fetch logs. Please try again later.");
      } finally {
        this.loading = false;
      }
    },
    handleDialogOpened() {
      this.$nextTick(() => {
        window.dispatchEvent(new Event("resize")); //  `el-table` 
      });
    },
    refreshTable() {
      this.tableKey += 1; //  `el-table` 
    },
    closeDialog() {
      this.$emit("update:visible", false);
    },
    // ****
    handlePageChange(newPage) {
      this.currentPage = newPage;
    },
  },
};
</script>

<style scoped>
.dialog-footer {
  display: flex;
  justify-content: flex-end;
}

